import React, { useState } from 'react';
import { Searchbar } from './component/Search'; 
import { Episodes } from './component/Episodelist';
import { Routes, Route } from 'react-router-dom';
import { Top } from './Top'; 
import './style.css';

function MyApp() {
  const [, setSelectedAnimeId] = useState(null);

  // Callback function to set the selected anime ID
  const handleAnimeSelect = (animeId) => {
    setSelectedAnimeId(animeId);
  };

  return (
    <div>
      <Routes>
      <Route path='shig/' element={<Top onAnimeSelect={handleAnimeSelect} />} />
      <Route path='shig/' element={<Searchbar onAnimeSelect={handleAnimeSelect} />} />
        <Route path='episodes/:animeId' element={<Episodes />} />
      </Routes>
    </div>
  );
}

export default MyApp;
