// Import necessary modules and styles
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { Searchbar } from './component/Search';
import './Top.css'; // Import your CSS file for styling

export function Top({ onAnimeSelect }) {
  const navigate = useNavigate();
  const url = "https://api-shiori.vercel.app/anime/gogoanime/top-airing";
  const [animes, setAnimes] = useState([]);
  const [selectedAnime, setSelectedAnime] = useState(null);

  const genres = ['Action', 'Adventure', 'Fantasy', 'Sci-Fi']; // Add your genres here

  const fetchData = async () => {
    try {
      const { data } = await axios.get(url, { params: { page: 1 } });
      return data;
    } catch (err) {
      throw new Error(err.message);
    }
  };

  const handleAnimeSelect = (anime) => {
    console.log(`Selected anime: ${anime.title} (ID: ${anime.id})`);
    onAnimeSelect(anime.id);
    setSelectedAnime(anime);
    navigate(`/episodes/${anime.id}`);
  };

  useEffect(() => {
    fetchData()
      .then((result) => {
        console.log(result);
        if (result.results && result.results.length > 0) {
          setAnimes(result.results);
        }
      })
      .catch((error) => {
        console.error(error);
      });

  }, []);

  return (
    <div>
      <Searchbar onAnimeSelect={handleAnimeSelect} />
      <div className="dev-recommendation">Dev Recommendation</div>
      <div className="search-results">
        {animes.map((anime) => (
          <div key={anime.id} className="search-results-item">
            <img src={anime.image} alt={`${anime.title} Poster`} />
            <p className="highlighted-title">{anime.title}</p>
            <p>Genres: {genres.join(', ')}</p>
            <button className="select-anime-btn" onClick={() => handleAnimeSelect(anime)}>
              Select Anime
            </button>
          </div>
        ))}
      </div>
      
    </div>
  );
}

export default Top;
