import React, { useState, useRef } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

export const Search = () => {
  return <div>Search</div>;
};

export function Searchbar({ onAnimeSelect }) {
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [showNoResultMessage, setShowNoResultMessage] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const baseApiUrl = "https://api-shiori.vercel.app/anime/gogoanime/";
  const navigate = useNavigate();
  const cancelToken = useRef(null);

  const fetchData = async (query, page) => {
    if (!query) {
      setSearchResults([]);
      return;
    }

    setIsLoading(true);

    const url = `${baseApiUrl}${query}?page=${page}`;
    cancelToken.current = axios.CancelToken.source();

    try {
      const { data } = await axios.get(url, { cancelToken: cancelToken.current.token });
      return data;
    } catch (err) {
      if (axios.isCancel(err)) {
        console.log('Request canceled');
      } else {
        console.error(err.message);
        setSearchResults([]);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const query = e.target.value;
    setSearchQuery(query);
    displaySearchResults(query);
  };

  const displaySearchResults = async (query) => {
    const data = await fetchData(query, 1);
    setSearchResults(data?.results || []);
    setShowNoResultMessage(true);
  };

  const handleAnimeSelect = (anime) => {
    console.log(`Selected anime: ${anime.title} (ID: ${anime.id})`);
    onAnimeSelect(anime.id);

    // Reset search-related state values
    setSearchQuery('');
    setSearchResults([]);
    setShowNoResultMessage(false);

    navigate(`/episodes/${anime.id}`);
  };

  return (
    <div className="search-container">
      <div className="search-bar">
        <input
          type="text"
          placeholder="Enter anime title"
          value={searchQuery}
          onChange={handleInputChange}
        />
        <button onClick={() => displaySearchResults(searchQuery)} disabled={isLoading}>
          {isLoading ? 'Searching...' : 'Search'}
        </button>
      </div>

      <div className="search-results">
        {isLoading && <div className="loading-spinner">Loading...</div>}

        {showNoResultMessage && searchResults.length === 0 && !isLoading && (
          <div className="no-result-message">No Result</div>
        )}

        {searchResults.map((result) => (
          <div key={result.id} className="search-results-item">
            <h2>{result.title}</h2>
            <p>ID: {result.id}</p>
            <img src={result.image} alt={`${result.title} Poster`} />
            <p>Release Date: {result.releaseDate || 'N/A'}</p>
            <p>Sub or Dub: {result.subOrDub}</p>
            <button className="select-anime-btn" onClick={() => handleAnimeSelect(result)}>
              Select Anime
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}
