import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Vidstream from '../Vidstream';
import { Searchbar } from './Search';
import { useParams, useNavigate } from 'react-router-dom';
import './Episodelist.css';
import './style.css';

export function Episodes() {
  const navigate = useNavigate();
  const { animeId } = useParams();
  const [episodeList, setEpisodeList] = useState([]);
  const [selectedEpisode, setSelectedEpisode] = useState(null);
  const [lastClickedEpisode, setLastClickedEpisode] = useState(null);

  const handleNextEpisode = () => {
    const selectedEpisodeIndex = lastClickedEpisode ? lastClickedEpisode.index : -1;
    const nextEpisodeIndex = selectedEpisodeIndex + 1;

    if (nextEpisodeIndex < episodeList.length) {
      const nextEpisode = episodeList[nextEpisodeIndex];
      setSelectedEpisode(nextEpisode);
      setLastClickedEpisode(nextEpisode);
    }
  };

  const fetchEpisodeList = async () => {
    try {
      const animeInfoUrl = `https://api-shiori.vercel.app/anime/gogoanime/info/${animeId}`;
      const response = await axios.get(animeInfoUrl);

      if (response.status === 200) {
        setEpisodeList(response.data.episodes.map((episode, index) => ({ ...episode, index })));
      } else {
        throw new Error(`Failed to fetch episode list. Status: ${response.status}`);
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchEpisodeList();
  }, [animeId]);

  const handleEpisodeSelect = (episode) => {
    console.log(`Clicked on episode: ${episode.title} (ID: ${episode.id})`);
    setSelectedEpisode(episode);
    setLastClickedEpisode(episode);
  };

  const handleAnimeSelect = (anime) => {
    console.log(`Selected anime: ${anime.title} (ID: ${anime.id})`);
    navigate(`/episodes/${anime.id}`);
  };

  return (
    <div className="App">
      <Searchbar onAnimeSelect={(anime) => { handleAnimeSelect(anime); fetchEpisodeList(); }} />
      <h1>Anime Episode List for {animeId}</h1>
      <div className="content-wrapper">
        {selectedEpisode && (
          <div className="video-player">
            <h2>Selected Episode: {selectedEpisode.title} ({selectedEpisode.id})</h2>
            <p>{selectedEpisode.description}</p>
            <Vidstream selectedEpisode={selectedEpisode} onNextEpisode={handleNextEpisode} />
          </div>
        )}
        <div className="episode-list-container" style={{ height: '300px', overflowY: 'auto' }}>
          <div className="episode-list">
            {episodeList.map((episode) => (
              <button
                className={`episode-button ${selectedEpisode?.id === episode.id ? 'selected' : ''}`}
                onClick={() => handleEpisodeSelect(episode)}
                key={episode.id}
              >
                {episode.index + 1} {episode.title}
              </button>
            ))}
          </div>
        </div>
        <div className="episode-navigation">
          {selectedEpisode && (
            <button className="episode-button" onClick={handleNextEpisode}>
              Next Episode
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
