import React, { useEffect, useState, useMemo } from 'react';
import axios from 'axios';
import ReactHLS from 'react-hls';
import './style.css';

function Vidstream({ selectedEpisode, onNextEpisode }) {
  const [videoUrl, setVideoUrl] = useState(null);
  const [selectedQuality, setSelectedQuality] = useState('1080p');

  const sourceMap = useMemo(() => ({
    '360p': 0,
    '480p': 1,
    '720p': 2,
    '1080p': 3,
    'Default': 3,
  }), []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (selectedEpisode) {
          const episodeId = selectedEpisode.id;
          const serverName = 'gogocdn';

          const url = `https://api-shiori.vercel.app/anime/gogoanime/watch/${episodeId}?server=${serverName}`;
          const response = await axios.get(url);

          if (response.status === 200) {
            const sources = response.data.sources;
            const sourceIndex = sourceMap[selectedQuality];

            if (sources[sourceIndex] && sources[sourceIndex].isM3U8) {
              setVideoUrl(sources[sourceIndex].url);
            } else {
              console.error('Selected quality source not found. Fallback to default.');
              const defaultSource = sources[3];
              if (defaultSource && defaultSource.isM3U8) {
                setVideoUrl(defaultSource.url);
                setSelectedQuality('1080p');
              } else {
                console.error('No default source available');
              }
            }
          } else {
            throw new Error(`Failed to fetch data. Status: ${response.status}`);
          }
        }
      } catch (err) {
        console.error(err);
      }
    };

    fetchData();
  }, [selectedEpisode, selectedQuality, sourceMap]);

  const handleQualityChange = (event) => {
    const newQuality = event.target.value;
    setSelectedQuality(newQuality);
  };

  const handleEnded = () => {
    onNextEpisode();
  };

  return (
    <div className="App">
      {videoUrl ? (
        <div>
          <ReactHLS
            url={videoUrl}
            controls={true}
            width="100%"
            onEnded={handleEnded}
          />
          <div>
            <label htmlFor="quality">Select Quality: </label>
            <select id="quality" value={selectedQuality} onChange={handleQualityChange}>
              {['360p', '480p', '720p', '1080p', 'Default'].map((quality, index) => (
                <option key={index} value={quality}>
                  {quality}
                </option>
              ))}
            </select>
          </div>
        </div>
      ) : (
        <p>Loading video...</p>
      )}
    </div>
  );
}

export default Vidstream;